/************************ PROFILE *************************/
.profile {
    position: relative;
    &.open {
        .btn-toogle-profile {
            -webkit-transform: rotate(-180deg);
            -moz-transform: rotate(-180deg);
            -ms-transform: rotate(-180deg);
            -o-transform: rotate(-180deg);
        }   
    }
    .btn-toogle-profile {
        position: absolute;
        top: 6px;
        right: 5px;
        font-size: 16px;
        padding: 15px;

        -webkit-transition: transform 0.2s ease;
        -moz-transition: transform 0.2s ease;
        -ms-transition: transform 0.2s ease;
        -o-transition: transform 0.2s ease;
        transition: transform 0.2s ease;
    }   
    .profile-trigger {
        padding: 15px;
    }
    .profile-container {
        display: none;
        border-top: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
    }
    .profile-nav {
        padding: 21px 15px;
        .profile-nav-item {
            font-family: $secondary-font-family;
            font-size: 12px;
            font-weight: 500;
            padding: 0 0 0 6px;
            margin: 0 0 16px 0;
            &:last-child {
                margin-bottom: 0;
            }
        }
        .profile-nav-link {
            i {
                color: $border-color;
                font-size: 16px;
                margin-right: 16px;
                vertical-align: middle;
            }
        }
    }
}
.person {
    .box-image {
        float: left;
        margin: 0 12px 12px 0;
        img {
            height: 30px;
            width: 30px;
            
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
        }
    }
    .box-content {
        min-height: 30px;
        padding: 2px 30px 0 30px;
        .name {
            font-size: 12px;
            font-weight: 700;
            margin: 0 0 2px 0;
        }
        .role {
            color: $terciary-color;
            font-size: 10px;
            font-weight: 700;
        }
    }
}

/************************ WIDGETS *************************/
.box-widget {
    padding: 0 15px;
    margin-bottom: 15px;
    .box-widget-internal {
        background-color: #FFF;
        border: 1px solid $border-color;
        position: relative;
    }
    @media (min-width: 991px) {
        padding: 0;
    }
}
/************************ BIRTHDAY *************************/
.box-widget-birthday {
    
    .birthday-header {
        background-color: #443775;
        padding: 14px 20px 0px 20px;
        height: 60px;
        position: relative;
        overflow: hidden;
        &:after {
            content: '\e90d';
            font-family: 'icomoon' !important;
            speak: none;
            position: absolute;
            bottom: 16px;
            right: -28px;
            color: rgba(255, 255, 255, 0.1);
            font-size: 80px;
            -webkit-transform: rotate(-15deg);
            -ms-transform: rotate(-15deg);
            transform: rotate(-15deg);
        }
        .birthday-icon {
            color: #FFF;
            font-size: 30px;
            float: left;
            margin: 0 16px 0 0;
        }
        .birthday-title {
            font-size: 14px;
            color: #FFF;
            font-weight: 700;
            font-family: $secondary-font-family;
            max-width: 200px;
        }
        .birthday-counter {
            font-size: 12px;
            background-color: #FFF;
            border-radius: 50%;
            height: 30px;
            width: 30px;
            color: #443775;
            text-align: center;
            position: absolute;
            top: 14px;
            right: 25px;
            padding: 5px 0 0 0;
            font-weight: 500;
        }
    }
        
    .birthday-footer {
        background-color: #fafafd;
        padding: 20px 0;
        text-align: center;
        .btn-all-birthdays {
            color: #443775;
            font-size: 12px;
            font-weight: 700;
        }
        .btn-all-birthdays span {
            text-decoration: underline;
            vertical-align: bottom;
        }
        .btn-all-birthdays i {
            font-size: 20px;
            vertical-align: middle;
            margin: 0 6px 0 0;
        }
    }     
    
    @media (min-width: 991px) {
        .birthday-header {
            .birthday-title {
                font-weight: 600;
                max-width: 400px;
                padding-top: 10px;
            }
            &:after {
                right: 4px;
            }
        }
    }
}

.box-birthday-today,
.box-birthday-ondemand {
    padding: 0 15px;
    margin-bottom: 40px;
    @media (min-width: 991px) {
        padding: 0;
        // margin: 0 -15px 0 -15px;
    }
}
.box-birthday-ondemand {
    @media (min-width: 991px) {
        margin: 0 -15px 0 -15px;
    }
}
.birthday-item {
    padding: 20px 25px;
    border-bottom: 1px solid $border-color;
    position: relative;
    vertical-align: top;
    .person {
        .box-content {
            min-height: 40px;
            padding-left: 40px;
            padding-right: 40px;
        }
        .box-image {
            margin: 0 18px 0px 0;
            img {
                height: 40px;
                width: 40px;
            }
        }
        .name {
            font-size: 14px;
            margin-bottom: 4px;
        }
        .role {
            color: $secondary-color;
            font-size: 12px;
            font-weight: 400;
        }
    }
    .btn-birthday-message {
        position: absolute;
        top: 13px;
        right: 5px;
        font-size: 26px;
        padding: 15px;
        color: $terciary-color;
        .tooltip {
            left: -20px;
        }
    }

    .box-birthday-today &,
    .box-birthday-ondemand & {
        background-color: #FFF;
        border: 1px solid $border-color;
        margin: 0 0 20px 0;
    }
    .box-birthday-ondemand & {
        .btn-birthday-message {
            display: none;
        }
        .person {
            .box-content {
                padding-right: 0;
            }
        }
    }

    @media (min-width: 991px) {
        .person {
            .name {
                &:hover {
                    a {
                        color: $primary-color;    
                    }
                }
            }
        }
        .btn-birthday-message {
            &:hover {
                color: $primary-color;
            }
        }

        .box-birthday-ondemand & {
            display: inline-block;
            margin: 0 15px 20px 15px;
            width: 45%;
        }
    }
}
/************************ LINKS *************************/
.box-widget-links {
    .links-header {
        padding: 20px 25px;
        position: relative;
        .links-title {
            color: $terciary-color;
            font-family: $secondary-font-family;
            font-size: 10px;
            text-transform: uppercase;
        }
    }
    .links-body {
        padding: 0 15px;
    }
    .link {
        display: inline-block;
        margin: 0 7px 8px 7px;
        vertical-align: top;
        .desc {
            font-size: 8px;
            font-weight: 700;
            color: gray;
            vertical-align: top;
            display: inline-block;
            line-height: normal;
            max-width: 46px;
        }
        .box-image {
            height: 46px;
            width: 46px;
            margin-bottom: 2px;
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
            img {
                height: auto;
                width: 100%;
            }
        }
    }
    @media (min-width: 991px) {
        .links-body {
            padding: 0 20px;
        }
        .links-header {
            padding: 30px 30px;
            .links-title {
                font-size: 12px;
            }
        }
        .link {
            margin: 0 9px 18px 9px;
            .desc {
                font-size: 10px;
            }
            .box-image {
                height: 60px;
                width: 60px;
                margin-bottom: 6px;
            }
        }
    }
}

/************************ TOOLTIP *************************/
.tooltip-trigger{
    position: relative;
}
.tooltip {
    display: none;

    background-color: $terciary-color;
    position: absolute;
    top: -36px;
    left: 0;
    color: #FFF;
    font-weight: 700;
    line-height: normal;
    padding: 8px 16px;
    border-radius: 5px;
    font-size: 10px;
    text-transform: uppercase;
    &:before {
        content: " ";
        position: absolute;
        bottom: -16px;
        left: 50%;
        border-color: $terciary-color transparent transparent transparent;
        border-style: solid;
        border-width: 8px 8px 8px 8px;
        margin-left: -4px;
    }
}

.box-logo-dbr {
    padding: 30px 0 40px 0;
    text-align: center;
    &.logo-dbr-desk {
        display: none;
    }
    .svg-dbr-mobo,
    .svg-dbr-desk,
    .svg-dbr-login {
        color: $primary-color;
        width: 22px;
        height: 22px;
        svg {
            width: inherit;
            height: inherit;
        }
        path {
            fill: currentColor;
        }
    }
    @media (min-width: 991px) {
        .container-nav & {
            position: absolute;
            bottom: 0;
            left: 50%;
            margin: -11px;
        }
        .container-nav-scroll & {
            position: relative;
            margin: 0;
            left: 0;
            padding: 30px 0 26px 0;
        }
        .svg-dbr {
            width: 22px;
            height: 22px;
        }
    }
}

// autocomplete
.ui-autocomplete {
    max-width: 420px;
    width: 99%;
    &.ui-autocomplete-search-general {
        max-height: 420px;
        overflow-y: auto;
    }
}

.autocomplete-result-search {
    /* background: red; */
    border: none;
    border-bottom: 1px solid $border-color;
    padding: 0;
    &.ui-menu-divider.ui-widget-content {
        display: none;
    }
    &:last-child {
        border-bottom: none;
    }
    .person {
        .box-image {
            img {
                height: 40px;
                width: 40px;
            }
        }
        .box-content {
            min-height: 40px;
            padding: 2px 60px 0 30px;
            .name {
                font-size: 14px;
                margin-top: 2px;
                margin-bottom: 4px;
            }
            .role {
                font-size: 12px;
                font-weight: 400;
            }
            .extension {
                position: absolute;
                top: 30px;
                right: 20px;
                font-size: 12px;
            }
        }
    }
}
.ui-widget.ui-widget-content {
    border: none !important;
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.1);
}
.ui-menu .ui-menu-item-wrapper {
    height: 80px;
    padding: 20px 25px;
    position: relative;
    &.ui-state-active {
        background-color: #fafafd;
        border: none;
        margin: 0;
        color: #000;
    }
}

.label-group {
    color: $terciary-color;
    font-family: $secondary-font-family;
    font-size: 16px;
    padding: 0 15px;
    margin: 0 0 20px 0;
    strong {
        text-transform: uppercase;
        margin: 0 8px 0 0;
        font-weight: 500;
        max-width: 80%;
        vertical-align: middle;
        display: inline-block;
        letter-spacing: 2px;
    }
    span {
        font-family: $font-family;
        font-size: 12px;
    }
    i {
        font-size: 15px;
        vertical-align: middle;
    }
    @media (min-width: 991px) {
        padding: 0;    
    }
}


.box-tabs {
    margin: -40px 0 0 0;
    .nav-tabs {
        height: 40px;
        li {
            display: inline-block;
            font-size: 13px;
            padding: 0 15px;
            a {
                border-bottom: 3px solid transparent;
                color: #848796;
                height: 40px;
                line-height: 40px;
                display: inline-block;
            }
            &.active {
                color: #fff;
                a {
                    border-color: #fff;
                    color: #fff;
                }
            }
        }   
    }
    .tab-content {
        width: 100%;
        clear: both;
        padding: 0;
        .tab-pane {
            display: none;
            padding-top: 26px;
        }
        .tab-pane.active {
            display: block;
        }
    }
    @media (min-width: 991px) {
        .tab-content {
            padding: 20px 0 0 0;
            .tab-pane {
                padding: 20px 0 0 0;
                &#tab-1 {
                    padding: 0;
                }
            }
        }
        .nav-tabs {
            li {
                &:first-child {
                    padding-left: 0;
                }
                &:hover {
                    color: #fff;
                    a {
                        color: #fff;
                    }
                }
            }   
        }
    }
}

// BOX REQUEST
.box-request {
    background-color: #FFF;
    border: 1px solid $border-color;
    margin: 0 15px 15px 15px;
    max-width: 480px;
    .box-request-header {
        background-color: #000;
        padding: 34px 20px 0 20px;
        height: 80px;
        border-bottom: 1px solid $border-color;
        .request-title {
            color: #FFF;
            font-family: $secondary-font-family;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
    }
    .request-item {
        border-bottom: 1px solid $border-color;
        color: $terciary-color;
        font-size: 12px;
        padding: 20px 20px;
        &:last-child {
            border: none;
        }
        strong {
            color: #000;
            display: block;
            font-size: 14px;
            margin: 0 0 2px 0;
        }
        .request-status {
            padding: 0 0 0 6px;
            i {
                font-size: 14px;
                display: inline-block;
                vertical-align: text-top;
                margin: 1px 4px 0 0;
            }
        }
        &.request-item-waiting {
            .request-status {
                color: $color-waiting;
            }
        }
        &.request-item-delivered {
            .request-status {
                color: $color-success;
            }
        }
        &.request-item-canceled {
            .request-status {
                color: $color-error;
            }
        }
    }

    @media (min-width: 480px) {
        margin: 0 auto 15px auto;
    }
    @media (min-width: 991px) {
        margin: 0;
        max-width: 480px;
    }

    .page-material-escritorio & {
        .box-request-header {
            background-color: $color-material-escritorio;
        }
        .request-item {
            strong {
                color: $color-material-escritorio;
            }
            
        }
    }
    .page-solicitacoes-ferias & {
        .box-request-header {
            background-color: $color-ferias;
        }
        .request-item {
            strong {
                color: $color-ferias;
            }
            
        }
    }
}

.box-request-new {
    background-color: #FFF;
    border: 1px solid $border-color;
    margin: -26px auto 15px auto;
    max-width: 480px;
    .box-request-new-header {
        padding: 34px 20px 0 20px;
        height: 80px;
        border-bottom: 1px solid $border-color;
        text-align: center;
        .request-title {
            color: #000;
            font-family: $secondary-font-family;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: 1px;
        }
    }
    .box-request-new-body {
        // border-bottom: 1px solid $border-color;
        .box-add-itens {
            padding: 24px 0;
        }
        .add-item {
            padding: 0 15px;
            margin-bottom: 30px;
            width: 100%;
            &:last-child {
                margin-bottom: 0;
            }
            .add-item-group {
                margin: 0  0 16px 0;
                &:last-child {
                    margin-bottom: 0;
                }
                &.add-item-group-datepicker {
                    position: relative;
                    &:after {
                        content: '\e902';
                        font-family: 'icomoon' !important;
                        speak: none;
                        position: absolute;
                        top: 48px;
                        right: 24px;
                        font-size: 22px;
                        color: $terciary-color;
                    }
                }
            }
            .label-label {
                font-size: 12px;
                font-weight: 700;
                display: block;
                margin: 0 0 8px 0;
            }
        }
        .box-btn {
            padding: 10px 15px 10px 15px;
        }
    }
    .box-request-new-footer {
        background-color: #fafafd;
        padding: 20px 0;
        text-align: center;
    }
    .acf-form-submit {
        margin: 0 -15px -24px -15px;
        border-top: 1px solid $border-color;
        position: relative;
    }
    .acf-field-repeater {
        >.acf-label {
            display: none !important;
        }
    }
    .acf-field {
        .acf-label {
            font-size: 12px;
            font-weight: 700;
            display: block;
            margin: 0 0 8px 0;
            .acf-required {
                display: none;
            }
        }
        .acf-field-radio {
            // clear: both;
            &.box-radio {
                padding: 18px 0;
            }
        }
    }
    .acf-spinner {
        position: absolute;
        top: 30px;
        left: 30px;
    }

    @media (min-width: 991px) {
        max-width: none;
        margin-top: 0;
        .box-request-new-header {
            text-align: left;
            padding-left: 40px;
            .request-title {
                font-weight: 600;
            }
        }
        .box-request-new-body {
            .box-add-itens {
                padding: 30px 0;
            }
            .add-item {
                padding: 0 30px;
                margin-bottom: 20px;
                .label-label {
                    display: none;
                }
                &:first-child {
                    .label-label {
                        display: block;
                    }
                }
                .add-item-group {
                    margin: 0;
                    &.add-item-group-datepicker,
                    &.add-item-group-radio {
                        .label-label {
                            display: block;
                        }
                        &.add-item-group-a,
                        &.add-item-group-b {
                            margin: 0;
                            width: 50%;
                            float: left;
                        }
                        &.add-item-group-b {
                            margin-left: -1px;
                        }
                    }
                }
                .add-item-group-a {
                    display: inline-block;
                    margin-right: 3%;
                    width: 64%;
                }
                .add-item-group-b {
                    display: inline-block;
                    width: 29%;
                    margin-left: 3%;
                }
            }
            .box-btn {
                padding: 15px 30px 15px 30px;
            }
        }
        .box-request-new-footer {
            padding: 20px;
            text-align: right;
        }
        .acf-form-submit {
            margin: 0 -30px -30px -30px;
        }
    }

    .page-material-escritorio & {
        .box-request-new-header {
            .request-title {
                color: $color-material-escritorio-secondary;
            }
        }
        .btn-primary {
            background-color: $color-material-escritorio-secondary;
        }
        .btn-add-item {
            color: $color-material-escritorio-secondary;
        }
        .ui-selectmenu-button.ui-button {
            color: $color-material-escritorio;
        }
        .ui-selectmenu-icon.ui-icon {
            border-color: $color-material-escritorio transparent transparent transparent;
        }
        a.ui-button:active, 
        .ui-button:active {
            border-color: $color-material-escritorio;
            background: #FFF;
        }
        .ui-spinner-input {
            color: $color-material-escritorio;
        }
        .ui-spinner .ui-spinner-button {
            background: $color-material-escritorio;
        }
        .datepicker {
            color: $color-material-escritorio;
        }
    }
    .page-solicitacoes-ferias & {
        .box-request-new-header {
            .request-title {
                color: $color-ferias-secondary; 
            }
        }
        .btn-primary {
            background-color: $color-ferias-secondary;
        }
        .btn-add-item {
            color: $color-ferias-secondary;
        }
        .ui-selectmenu-button.ui-button {
            color: $color-ferias;
        }
        .ui-selectmenu-icon.ui-icon {
            border-color: $color-ferias transparent transparent transparent;
        }
        a.ui-button:active, 
        .ui-button:active {
            border-color: $color-ferias;
            background: #FFF;
        }
        .ui-spinner-input {
            color: $color-ferias;
        }
        .ui-spinner .ui-spinner-button {
            background: $color-ferias;
        }
        .datepicker {
            color: $color-ferias;
        }
    }
}

// fancybox modal
.modal {
    display: none;
}
.fancybox-content {
    padding: 0;
    width: 300px;
    .modal-header {
        border-bottom: 1px solid $border-color;
        height: 80px;
        padding: 34px 34px;
        .modal-header-title {
            font-family: $secondary-font-family;
            font-size: 12px;
            letter-spacing: 1px;
            text-transform: uppercase;
        }
    }
    .modal-body {
        border-bottom: 1px solid $border-color;
        padding: 25px;
        .form-item {
            margin: 0 0 20px 0;
            .label {
                font-size: 12px;
                font-weight: 700;
                margin: 0 0 6px 0;
                display: block;
            }
            .input-text {
                background-color: #FFF;
                border-color: $border-color;
                height: 60px;
                width: 100%;
                padding: 0 0 0 12px;
            }
            .select2-selection {
                background-color: #FFF;
                border-color: $border-color;
                height: 60px;
                width: 100%;
                border-radius: 0;
                font-size: 14px;
                line-height: 1.4;
                .select2-selection__rendered {
                    height: 60px;
                    line-height: 60px;
                    // padding: 0 0 0 12px;
                    .select2-selection__placeholder {
                        
                    }
                }
                .select2-selection__arrow {
                    top: 16px;
                    right: 16px;
                }
            }
        }
        .box-request-new-footer {
            background-color: #fafafd;
            margin: 0 -25px -25px -25px;
            text-align: right;
            padding: 25px;
            border-top: 1px solid $border-color;
        }
    }
    .modal-footer {
        background-color: #fafafd;
        padding: 20px;
        text-align: right;
        .btn {
            display: inline-block;
            margin-left: 15px;
            vertical-align: top;
        }
    }
    .acf-form-submit {
        position: relative;
        .acf-spinner {
            position: absolute;
            top: 10px;
            right: 0;
            left: auto;
        }
    }
    &.modal-feedback {
        .fancybox-close-small {
            top: 10px;
            right: 10px;
        }
        .modal-body {
            padding: 25px 34px 25px 34px;
        }
        .box-modal-feedback {
            padding: 20px;
            text-align: center;
            width: 100%;
            .icon {
                margin-bottom: 20px;
                i {
                    font-size: 60px;
                }
            }
            .title {
                font-size: 26px;
                margin-bottom: 20px;
                font-family: $secondary-font-family;
                font-weight: 500;
            }
            .text {
                font-size: 14px;
                line-height: 20px;
            }
        }
    }
    .page-material-escritorio & {
        .modal-header {
            .modal-header-title {
                color: $color-material-escritorio-secondary;
            }
