table.table-default {
    background-color: #FFF;
    border: 1px solid $border-color;
    width: 100%;
    margin-bottom: 20px;
    thead {
        th {
            background-color: #fafafd;
            padding: 10px 20px;
            border-bottom: 1px solid $border-color;
            font-size: 12px;
            font-weight: 400;
            text-align: left;
        }
    }
    tbody {
        td {
            border-bottom: 1px solid $border-color;
            font-size: 14px;
            font-weight: 700;
            padding: 20px 20px;
            position: relative;
            .span {
                display: block;
                font-size: 12px;
                font-weight: 400;
                &.unavailable {
                    color: $color-error;
                    font-size: 14px;
                    font-weight: 700;
                }
            }
            .btn-expand {
                color: $terciary-color;
                position: absolute;
                top: 50%;
                right: 20px;
                margin-top: -8px;
                font-size: 16px;
                i {
                    -webkit-transform: rotate(180deg);
                    -moz-transform: rotate(180deg);
                    -ms-transform: rotate(180deg);
                    -o-transform: rotate(180deg);

                    position: absolute;
                    top: 0;
                    right: 0;
                }
                    
            }
        }
        .tr-expanded {
            td {
                // color: #36bcf1;
                border-bottom: none;
                padding-bottom: 0px;
                .btn-expand {
                    margin-top: -16px;
                    i {
                        color: #36bcf1;
                        -webkit-transform: rotate(270deg);
                        -moz-transform: rotate(270deg);
                        -ms-transform: rotate(270deg);
                        -o-transform: rotate(270deg);
                        top: 10px;
                    }
                }
                .hide-on-expanded {
                    visibility: hidden;
                }
                &.color-on-expanded {
                    color: #36bcf1;
                }
            }

        }
        .td-expanded {
            padding: 5px 20px 20px 20px;
            font-weight: 400;
            vertical-align: top;
            .text {
                margin: 0 0 20px 0;
                // color: #36bcf1;
                font-weight: 700;
            }
            .text-select {
                width: 70%;
            }
            .btn.btn-edit {
                // color: #36bcf1;
                font-size: 26px;
            }
            .span {
                display: block;
            }
            span {
                font-weight: 400;
                color: #000;
                margin: 0 0 6px 0;
            }
            &.color-on-expanded {
                .text,
                .btn.btn-edit {
                    color: #36bcf1;
                }
            }
        }
    }
    &.table-materiais {
        tbody {
            .tr-expanded {
                td {
                    padding-bottom: 20px;
                }
            }
        }
    }
    &.no-footer {
        border-color: $border-color;
        border-width: 1px 1px 0 1px;
        border-style: solid;
    }
    @media (max-width: 991px) {
        &.table-materiais {
            tbody {
                .ui-selectmenu-button.ui-button {
                    width: 80%;
                }
            }
        }
    }
    @media (min-width: 991px) {
        .page-material-escritorio & {
            tbody {
                tr {
                    &:hover {
                        color: $color-material-escritorio-secondary;
                        .btn {
                            color: $color-material-escritorio-secondary;
                        }
                    }
                }
            }
        }
        .page-solicitacoes-ferias & {
            tbody {
                tr {
                    &:hover {
                        color: $color-ferias-secondary;
                        .btn {
                            color: $color-ferias-secondary;
                        }
                    }
                }
            }
        }
    }
}
