$primary-color:     #f13d35; //red
$secondary-color:   #222329; //black
$terciary-color:    #848796; //gray
$border-color:      #c7cad8; //gray border

$color-facebook: #4870a7;
$color-twitter: #59c8f2;
$color-whatsapp: #02c43e;

$color-aniversarios:        #443775; // pagina aniverarios
$color-material-escritorio: #2b4558; // pagina material de escritorio
$color-material-escritorio-secondary: #36bcf1; // pagina material de escritorio
$color-ferias:              #582b41; // pagina ferias
$color-ferias-secondary:      #c74986; // pagina ferias
$color-conteudo:              #35363f; // pagina conteudo
$color-conteudo-live:         #18191f; // pagina conteudo
$color-conteudo-secondary:    red; // pagina conteudo
$color-calendario:              #000; // pagina conteudo

$color-success: #04d14c;
$color-error:   #f13d35;
$color-waiting: #d1ad04;

$body-bg:          #FFF !default;
$text-color:       #000 !default;

$font-family:       'Lato', sans-serif;
$secondary-font-family:       'Montserrat', sans-serif;
$font-weight:       400;
$font-size:         13px !default;
$line-height:       140% !default;

$link-color:                $text-color !default;
$link-hover-color:          #CCC !default;
$link-hover-decoration:     none !default;

$screen-xs-min:     480px !default;
$screen-sm-min:     768px !default;
$screen-md-min:     992px !default;
$screen-lg-min:     1200px !default;

$screen-xs-max:     ($screen-sm-min - 1) !default;
$screen-sm-max:     ($screen-md-min - 1) !default;
$screen-md-max:     ($screen-lg-min - 1) !default;

$grid-columns:      12 !default;
$grid-gutter:       30px !default;

$container-sm:      (720px + $grid-gutter) !default;
$container-md:      (940px + $grid-gutter) !default;
$container-lg:      (1160px + $grid-gutter) !default;


