#calendar {
    padding: 20px 15px;
    border-radius: 0;
    .calendar-header {
        display: none;
    }
    .months-container {
        .day {
            //&.day-feriado-nacional {
            //    background: #04d1a8;
            //}
            //&.day-feriado-regional {
            //    background: #36bcf1;
            //}
            //&.day-nao-trabalhamos-cc {
            //    background: #926cde;
            //}
            //&.day-data-de-pagamento {
            //    background: #e7e014;
            //}
            //&.day-ponte-recesso {
            //    background: #f2982e;
            //}
            //&.day-inicio-atividades-2019 {
            //    background: #f45ace;
            //}
            &.day-unchecked {
                .day-content {
                    border-color: transparent !important;
                }               
            }
        }
        table.month {
            .month-title {
                font-family: $secondary-font-family;
                font-size: 16px;
                font-weight: 400;
                text-transform: uppercase;
                padding: 30px 0;
            }
            tr {
                th {
                    font-family: $secondary-font-family;
                    font-size: 12px;
                    font-weight: 400;
                    color: #000;
                    padding: 0 0 15px 0;
                }
                td {
                    font-family: $secondary-font-family;
                    font-size: 12px;
                    font-weight: 400;
                    color: $terciary-color;
                    .day-content {
                        border-radius: 0;
                        height: 36px;
                        width: 36px;
                        margin: 1px;
                        padding: 7px 0 0 0;
                        border: 2px solid transparent;
                        position: relative;
                        &:hover {
                            background-color: inherit;
                        }
                        .event-tooltip-wrapper {
                            background-color: #FFF;
                            position: absolute;
                            bottom: 50px;
                            left: 50%;
                            width: 210px;
                            margin-left: -105px;
                            border: 1px solid $border-color;
                            border-radius: 4px;
                            padding: 24px;
                            text-align: left;
                            z-index: 1;
                            display: none;
                            .circle {
                                display: none;

                                background: red;
                                height: 4px;
                                width: 4px;
                            }
                            &.event-tooltip-wrapper-has-circle {
                                .circle {
                                    display: inline-block;
                                }
                            }
                            &:after {
                                content: " ";
                                position: absolute;
                                bottom: -6px;
                                left: 50%;
                                border-width: 1px;
                                border-style: solid;
                                border-color: transparent $border-color $border-color transparent;
                                height: 12px;
                                width: 12px;
                                background: #FFF;
                                transform: rotate(45deg);
                                margin-left: -6px;
                            }
                            .event-tooltip-content {
                                margin: 0 0 12px 0;
                                font-family: $font-family;
                                font-size: 12px;
                                font-weight: 700;
                                .event-name {
                                    line-height: 1.2;
                                    margin: 0 0 4px 0;
                                    text-transform: uppercase;
                                }
                                .event-location {
                                    color: #000;
                                }
                            }
                        }
                    }
                }
            }
                
        }
    }
    @media (min-width: 991px) {
        padding: 40px 0;
        overflow: visible;
        margin-top: 160px;
    }
}
.circle-wrapper {
    position: absolute;
    /* background: black; */
    bottom: 0;
    height: 8px;
    vertical-align: top;
    width: 100%;
}

.circle {
    height: 3px;
    width: 3px;
    border-radius: 5px;
    display: inline-block;
    background: black;
    margin: 2px 1px 0 1px;
    vertical-align: top;
}

.months-container {
    box-sizing: border-box;
}