.dropdown-filter-dropdown {
    position:relative;
    display:inline-block;
}

.dropdown-filter-icon {
    margin: 0 0 0 8px;
    position: relative;
    vertical-align: bottom;
    display: inline-block;
    height: 14px;
    width: 12px;
}


.dropdown-filter-icon:hover {
    cursor:pointer;
}

.checkbox-container {
    max-height: 400px;
    overflow-y: scroll;
}
.dropdown-filter-content {
    display: none;
    position: absolute;
    background-color: #FFF;
    min-width: 200px;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
    z-index: 1;
    padding: 10px 0 0 0;
}

.dropdown-filter-content div {
    margin-top:5px;
    margin-bottom:5px;
    margin-left: 10px;
    margin-right: 10px;
}

.dropdown-filter-content div.dropdown-filter-search input {
    height: 40px;
    width: 100%;
    padding-left: 8px;
}
.dropdown-filter-content div.dropdown-filter-search {
    margin-bottom:10px;
    margin-top:10px;
}


.dropdown-filter-content div.dropdown-filter-sort {
    padding-top:5px;
    padding-bottom: 10px;
}

.dropdown-filter-content div.dropdown-filter-sort:hover {
    background-color:#e1e5e7;
    cursor:pointer;
}

.dropdown-filter-content div.dropdown-filter-sort span {
    margin-right:5px;
    margin-left:5px;
    margin-top:5px;
    margin-bottom:5px;
    color:#000000;
}

.arrow-down {
    border-top: 2px solid $primary-color;
    width: 12px;
    height: 8px;
    position: relative;
    display: inline-block;
    vertical-align: top;
    &:before {
        content: " ";
        position: absolute;
        top: 2px;
        height: 2px;
        width: 8px;
        background: red;
        left: 2px;
        border-radius: 2px;
    }
    &:after {
        content: " ";
        position: absolute;
        top: 6px;
        height: 2px;
        width: 4px;
        background: red;
        left: 4px;
        border-radius: 2px;
    }
}
