.page-login {
    background: url(../images/bg-login.png);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0;
    .section-main {
        padding: 80px 0 0 0;
    }
    .box-logo-dbr {
        position: absolute;
        bottom: 0;
        left: 50%;
        margin: 0 0px 0 -10px;
    }
    .login {
        text-align: center;
        margin: 0 auto 24px auto;
        max-width: 360px;

        .title {
            display: inline-block;
            margin: 0 0 25px 0;
            a {
                background: url(../images/almap-login.png);
                background-repeat: no-repeat;
                background-size: cover;
                height: 25px;
                width: 224px;
                display: block;
                text-indent: -9999px;
            }
        }
        .text {
            color: $terciary-color;
            margin: 0 30px 50px 30px;
            font-style: italic;
            line-height: 20px;
        }
        .input {
            background-color: #32323A !important;
            height: 60px;
            border: none;
            font-size: 14px;
            color: #FFF;

            border-width: 1px;
            border-style: solid;
            border-color: #474953;
            margin: -1px 0 0 0;
            height: 60px;
            text-align: center;
            width: 100%;
        }
        .button-primary {
            background-color: $primary-color;
            font-family: $secondary-font-family;
            font-weight: 600;
            font-size: 14px;
            border: none;
            height: 60px;
            color: #FFF;
            text-transform: uppercase;
            padding: 0 60px;
        }
        div>label,
        p>label {
            display: none;
        }
        .login-password {
            position: relative;
        }
        .login-submit {
            margin-top: 30px;
        }
        .show-password {
            position: absolute;
            top: 0;
            right: 0;
            cursor: pointer;
            height: 60px;
            width: 60px;
            padding: 14px 0 0 0;
            .icon-eye {
                color: #474953;
                font-size: 30px;
            }
            &.show-password-active .icon-eye {
                color: #FFF;
            }
        }
    }
    @media (min-width: 991px) {
        .login {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            min-width: 360px;
            .title {
                a {
                    // height: 160px;
                    // width: 480px;
                }
            }
        }
    }
}