/************** SECTION MAIN ***************************/
@media (max-width: 991px) {
    .container,
    [class*='col-'] {
        padding-left: 0px;
        padding-right: 0px;
    }
    .row {
        margin-left: 0px;
        margin-right: 0px;
    }
    .container {
        margin-left: auto !important;
        margin-right: auto !important;
    }
}