    }
}
.feed {
    // background-color: #FFF;
    // border: 1px solid $border-color;
    position: relative;
    margin: 0 0 20px 0;
    .feed-label {
        margin: 0 0 24px 0;
    }
}
.page-load-status {
    display: none;
}

.article {
    margin-bottom: 15px;
    .article-header {
        background-color: #FFF;
        border: 1px solid $border-color;
        border-bottom: none;
        padding: 14px 18px;
        margin: 0 15px;
        .box-content {
            padding: 0px 0px 0 30px;
            .name {
                font-size: 14px;
                display: inline-block;
                margin-bottom: 0;
            }
            .share-link {
                color: $terciary-color;
                font-size: 14px;
                display: inline-block;
                a {
                    color: $primary-color;
                    font-weight: 700;
                }
            }
            .time {
                color: #aeb1be;
                font-size: 12px;
            }
        }
    }
    .article-body {
        .article-body-text {
            background-color: #FFF;
            border-left: 1px solid $border-color;
            border-right: 1px solid $border-color;
            margin: 0 15px;
            padding: 0 0 15px 0;
            p {
                color: $terciary-color;
                font-size: 14px;
                line-height: 20px;
                padding: 0 18px;
            }
        }
        .article-body-link-preview {
            border-top: 1px solid $border-color;
            // border-bottom: 1px solid $border-color;
            .link-preview {
                .box-image {
                    // height: 170px;
                    // background-size: cover;
                    // background-position: center center;

                    
                    border-bottom: 1px solid $border-color;
                    background-size: contain;
                    background-repeat: no-repeat;
                    text-align: center;
                    a {
                        display: block;
                        // text-indent: -9999px;
                        height: 100%;
                        width: 100%;
                    }
                    img {
                        // max-width: 100%;
                        width: 100%;
                        height: auto;
                    }
                }
                .box-content {
                    padding: 12px 15px;
                    .link {
                        color: $terciary-color;
                        font-size: 10px;
                    }
                    .title {
                        font-size: 14px;
                        font-weight: 700;
                        line-height: normal;
                        margin: 0 0 6px 0;
                    }
                    .text {
                        color: $terciary-color;
                        font-size: 12px;
                    }
                }
                &.link-preview-video {
                    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.1);
                    overflow: hidden;
                    height: 95px;
                    &:before,
                    &:after {
                        content: " ";
                        display: table;
                        width: 100%;
                    }
                    .box-image {
                        float: left;
                        height: 100%;
                        width: 30%;
                    }
                    .box-content {
                        float: left;
                        width: 70%;
                    }
                }
            }
        }
    }
    .article-footer,
    .page-footer-interactions {
        background-color: #FFF;
        border: 1px solid $border-color;
        // border-top: none;
        margin: 0 15px;
        .article-footer-interactions {
            border-bottom: 1px solid $border-color;
            padding: 12px 0 15px 0;
            position: relative;
            .box-like {
                float: left;
                padding: 0px 15px 0px 15px;
                span {
                    font-size: 14px;
                    color: $primary-color;
                    display: inline-block;
                    vertical-align: middle;
                }
                i {
                    color: $primary-color;
                    font-size: 16px;
                    margin: 0 2px 0 0;
                    vertical-align: middle;
                }
                .wpulike-robeen {
                    svg {
                        width: 30px;
                        margin: 0px 0 0 -4px;
                    }
                    .count-box {
                        font-size: 16px !important;
                        vertical-align: middle !important;
                        top: -11px;
                        bottom: auto;
                        right: 2px;
                    }
                    .wp_ulike_likers_wrapper {
                        display: none;
                    }
                }
            }
            .btn-trigger-comments {
                display: none;
                position: absolute;
                top: 0;
                right: 0;
                width: 190px;
                margin: 0;
                border-left: 1px solid $border-color;
                font-weight: 700;
            }
        }
        .box-like-people {
            font-size: 10px;
            color: $terciary-color;
            padding: 2px 0 0 0;
            a {
                color: #000;
                font-weight: 700;
            }
            .people-photos {
                float: left;
                padding: 0 0 0 12px;
                margin: 0 12px 0px 0;
                img {
                    height: 27px;
                    width: 27px;
                    border-radius: 50%;
                    border: 2px solid #FFF;
                    margin: 0 0 0 -12px;
                }
            }
            .people-links {
                line-height: 1.1;
                padding: 2px 10px 0 0;
            }
        }
        &.article-footer-comments-open {
            // background-color: #fafafd;
            .btn-trigger-comments {
                background-color: #fafafd;
                // border-bottom: 1px solid $border-color;
                i, span {
                    color: #000;    
                }                    
            }
            .btn-trigger-more-comments {
                border-bottom: 1px solid $border-color;
            }
            .article-footer-comments {
                border-top: 1px solid $border-color;
                background-color: #fafafd;
            }
        }
        .article-footer-comments {
            // padding: 15px 0 15px 0;
            // text-align: center;
            .comments-hidden {
                display: none;
            }
            
            .comment {
                border-bottom: 1px solid $border-color;
                padding: 15px;
                .name {
                    margin: 0;
                }
                .time {
                    color: #aeb1be;
                    font-size: 10px;
                    vertical-align: top;
                }
                .comment-text {
                    margin: 0 15px;
                    clear: both;
                    p {
                        color: $terciary-color;
                        font-size: 14px;
                        line-height: 20px;
                    }
                }
            }
            .comment-editor {

            }
        }

    }
    @media (min-width: 991px) {
        margin-bottom: 24px;
        .article-header,
        .article-footer, 
        .article-body .article-body-text {
            margin: 0;
        }
        .article-header {
            padding: 16px 18px;
        }
        .article-body {
            background-color: #FFF;
            border: 1px solid $border-color;
            border-top: none;
            padding-bottom: 20px;
            .article-body-text {
                border: none;
            }
            .article-body-link-preview {
                // border: 1px solid $border-color;
                box-shadow: 2px 2px 10px rgba(0,0,0,0.2);
                margin: 0 15px;
                .link-preview {
                    .box-image {
                        // height: 278px;
                    }
                    .box-content {
                        padding: 20px 20px 40px 20px;
                        position: relative;
                        .title {
                            font-size: 18px;
                            margin: 0 0px 16px 0;
                        }
                        .text {
                            font-size: 14px;
                            margin: 0 0px 16px 0;
                        }
                        .link {
                            font-size: 12px;
                            font-weight: 700;
                            position: absolute;
                            bottom: 20px;
                            left: 20px;
                        }
                    }
                    &.link-preview-video {
                        height: 170px;
                        .box-content {
                            height: 100%;
                        }
                    }
                }
            }
        }
        .article-footer {
            // height: 60px;
            border-top: none;
            .article-footer-interactions {
                // width: 68%;
                // float: left;
                border-bottom: none;
                padding: 15px 0 15px 0;
                height: 60px;
                // border-right: 1px solid $border-color;
                .btn-trigger-comments {
                    display: block;
                }

            }
            .article-footer-comments {
                // float: left;
                // width: 32%;
                .btn-trigger-comments {
                    display: none;
                }
                &.article-footer-comments-open {
                    border-top: 1px solid $border-color;
                }
            }
            .box-like-people {
                max-width: 70%;
            }
        }
    }
    @media (max-width: 991px) {
        .article-body {
            .article-body-link-preview {
                border-top: 1px solid #c7cad8;
                border-bottom: 1px solid #c7cad8;
                margin-bottom: -1px;
            }
        }
        .article-footer {
            .article-footer-interactions {
                height: 57px;
            }
            .article-footer-comments {
                .btn-trigger-comments {
                    font-weight: 700;
                }
                .comment {
                    .comment-text {
                        margin: 0;
                    }
                }
            }
            &.article-footer-comments-open {
                .article-footer-comments {
                    border-top: none;
                }
                .comments-hidden {
                    border-top: 1px solid $border-color;
                }
            }
        }
    }
}
.tags {
    display: inline-block;
    .tag {
        background-color: #aaa;
        font-family: $secondary-font-family;
        font-weight: 600;
        font-size: 8px;
        color: #FFF;
        padding: 1px 4px;
        border-radius: 4px;
        text-transform: uppercase;
        &.tag-0 { background-color: transparent; color: #000; font-size: 12px; }
        &.tag-1 { background-color: #55dc75; }
        &.tag-2 { background-color: #dcae55; }
    }
}

