.article-footer-comments {
    position: relative;
    .comment-editor {
        background-color: #FFF;
        padding: 18px 16px;
        .comment-form {
            position: relative;
        }
        .comment-form-comment {
            float: left;
            width: 85%;
        }
        .box-person {
            float: left;
            width: 15%;
            .person {
                .box-content {
                    display: none;
                }
                .box-image {
                    margin: 0;
                }
            }
        }
        .acf-field-image {
            // position: absolute;
            // top: 0px;
            // right: 10%;
            .acf-image-uploader {
                position: absolute;
                top: 10px;
                right: 10%;
                &.has-value {
                    height: 50px;
                    width: 50px;
                    top: 0px;
                }
            }
        }
        
        textarea {
            border: 1px solid $border-color;
            width: 100%;
            height: 70px;
            text-align: left;
            margin: 0 auto;
            padding: 16px 50px 0 18px;
            font-size: 14px;
        }
        label {
            display: none;
        }
        .form-submit {
            padding: 14px 0 0 0;
            text-align: right;
            clear: both;
            .submit {
                background: $primary-color;
                font-weight: 700;
                line-height: 40px;
                font-family: $secondary-font-family;
                font-size: 12px;
                height: 40px;
                padding: 0 24px;
                text-align: center;
                text-transform: uppercase;
                color: #FFF;
                border: none;
                cursor: pointer;
            }
        }
        .acf-label {
            display: none;
        }
        .acf-image-uploader {
            .hide-if-value {
                font-size: 0;
            }
        }
        .acf-button {
            position: absolute;
            height: 24px;
            width: 24px;
            z-index: 1;
            cursor: pointer;
            font-size: 0;
            top: 16px;
            right: -6px;
            margin: 0;
            left: auto;
            &:before {
                content: '\e91f';
                font-family: 'icomoon' !important;
                speak: none;
                position: absolute;
                top: 0;
                left: 0;
                color: $terciary-color;
                font-size: 24px;
            }
        }
    }
    @media (min-width: 991px) {
        .comment-editor {
            .box-person {
                width: 7%;
            }
            .comment-form-comment {
                width: 93%;
            }
            textarea {
                height: 50px;
                padding: 15px 70px 0 18px;
            }
            .acf-button {
                right: -40px;
            }
        }
    }
}
    
