/******************************/
/******** PADROES **********/
/******************************/

/* 
Roboto
thin 100
light 300
regular 400
medium 500
bold 700
black 900
*/

body {
    background-color: #f4f5f8;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    color: $secondary-color;
    font-weight: 400;
    padding-top: 60px;

    -webkit-text-size-adjust:100%; 
    -moz-text-size-adjust:100%; 
    -ms-text-size-adjust:100%;
}
h1, h2, h3, h4, h5, h6 {
    color: $secondary-color;
    margin-top: 0;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.08;
}
ul, ol {
    list-style: none;
    margin: 0;
    padding: 0;
}
a {
    color: $secondary-color;
    // -webkit-transition: color .3s ease;
    // -moz-transition: color .3s ease;
    // -khtml-transition: color .3s ease;
    // transition: color .3s ease;

    text-decoration: none;
}
// a:hover,
// a:focus  {
//     color: $secondary-color;
//     text-decoration: none;
//     outline: none;
// }

.fa {
  display: inline-block;
  line-height: 1;
}
p {
    line-height: 120%;    
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
*:before, *:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
.img-rounded {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
}
button {
    background: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
}
/******************************/
/********** FORM *************/
/******************************/
::-webkit-input-placeholder { color: #D6D6D6 !important; }
:-moz-placeholder { color: #D6D6D6 !important; }
::-moz-placeholder { color: #D6D6D6 !important; }
:-ms-input-placeholder { color: #D6D6D6 !important; }
input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px white inset;
}

legend, .input-submit-hidden { display: none; }

.input-text,
.textarea {
    background: #FAFAFA;
    border: 1px solid transparent;
    height: 42px;
    padding: 0 0 0 16px;
    width: 100%;
    color: #000000;
    font-size: 16px;
    -webkit-transition: border-color 0.2s ease;
    -moz-transition: border-color 0.2s ease;
    -ms-transition: border-color 0.2s ease;
    -o-transition: border-color 0.2s ease;
    transition: border-color 0.2s ease;
}
.textarea {
    height: 140px;
    padding: 8px 0 0 14px;
}
fieldset {
    border: none;
    padding: 0;
    margin: 0;
}

textarea,
input[type="text"],
input[type="password"],
input[type="button"],
input[type="submit"],
input[type="file"], 
input[type="datetime"], 
input[type="datetime-local"], 
input[type="date"], 
input[type="month"], 
input[type="time"], 
input[type="week"], 
input[type="number"], 
input[type="email"], 
input[type="url"], 
input[type="search"], 
input[type="tel"], 
input[type="color"] {
    -webkit-appearance: none;
    border-radius: 0;
}
.input-text:focus, 
.textarea:focus, 
textarea:focus, 
input[type="text"]:focus, 
input[type="password"]:focus, 
input[type="datetime"]:focus, 
input[type="datetime-local"]:focus, 
input[type="date"]:focus, 
input[type="month"]:focus, 
input[type="time"]:focus, 
input[type="week"]:focus, 
input[type="number"]:focus, 
input[type="email"]:focus, 
input[type="url"]:focus, 
input[type="search"]:focus, 
input[type="tel"]:focus, 
input[type="color"]:focus, 
.uneditable-input:focus {  
    box-shadow: none;
    outline: 0 none;
    border-color: #000;
}
@media screen and (-webkit-min-device-pixel-ratio:0) { 
  select,
  textarea,
  input {
    font-size: 16px;
  }
}
/******************** BOTOES ***********************/
.btn {
    color: #fff;
    cursor: pointer;
    >i {

    }
    &.btn-icon {
        color: #51525e;
        font-size: 24px;
        display: inline-block;
        margin: 0 4px;
    }
    // &.btn-primary {
    //     border: 1px solid #525252;
    //     font-size: 12px;
    //     padding: 3px 12px;
    //     margin: -8px 0 0 0;
    // }
    &.btn-more {
        font-size: 20px;
        background-color: $terciary-color;
        border-radius: 50%;
        height: 36px;
        width: 36px;
        text-align: center;
    }
    &.btn-edit {
        color: #aeb1be;
        font-size: 24px;
    }
    &.btn-add-item {
        color: #000;
        font-weight: 700;
        font-size: 14px;
        i {
            margin: 0 8px 0 0;
            font-size: 16px;
            vertical-align: middle;
        }
    }
    &.btn-primary {
        font-family: $secondary-font-family;
        font-weight: 700;
        font-size: 12px;
        background-color: #000;
        line-height: 40px;
        height: 40px;
        text-align: center;
        padding: 0 24px;
        text-transform: uppercase;
        border: none;
        i {
            margin: 0 4px 0 0
        }
        // &:hover,
        // &:focus {
        //     background-color: inherit;
        //     color: #FFF;
        // }
    }
    &.btn-secondary {
        background-color: #FFF;
        border: 2px solid #000;
        color: #000;
        font-family: $secondary-font-family;
        font-weight: 500;
        font-size: 12px;
        line-height: 38px;
        height: 40px;
        text-align: center;
        padding: 0 24px;
        text-transform: uppercase;
        i {
            margin: 0 4px 0 0
        }
    }
    &.btn-top {
        position: absolute;
        top: 142px;
        left: 50%;
        margin-left: -75px;
        font-family: "Montserrat";
        font-weight: 600;
        border: 3px solid #FFF;
        height: 60px;
        line-height: 58px;
        text-transform: uppercase;
        padding: 0 30px;
        z-index: 1;
        @media (min-width: 991px) {
            top: 50px;
            right: 50px;
            left: auto;
            margin-left: 0;
        }
    }
    &.btn-trigger-comments,
    &.btn-trigger-more-comments {
        // border-bottom: 1px solid $border-color;
        display: block;
        height: 60px;
        margin: 0 4px 0 0;
        text-align: center;
        width: 100%;
        i {
            color: $terciary-color;
            font-size: 16px;
            margin: 0 2px 0 0;
            vertical-align: middle;
        }
        span {
            color: $terciary-color;
        }
    }
    &.btn-trigger-comments {
        height: 60px;
    }
    &.btn-trigger-more-comments {
        height: 45px;
        text-decoration: underline;
        display: none;
    }
}
.box-btn-load-more {
    
}

.pagination-container {
    text-align: center;
    padding: 15px 0;

    &.pagination-loading {
        .btn-more {
            position: relative;
            &:before {
                content: " ";
                position: absolute;
                top: 0;
                left: 0;
                background-image: url(../images/spinner@2x.gif);
                background-size: cover;
                height: 36px;
                width: 36px;
                z-index: 1;
            }
        }
    }
    @media (min-width: 991px) {
        padding: 0;
        margin-bottom: 30px;
    }
}

.ui-widget {
    font-family: $font-family;
}
// jqyer ui - select
.ui-selectmenu-button.ui-button {
    color: #000;
    height: 60px;
    line-height: 60px;
    background: transparent;
    border-radius: 0;
    padding: 0 20px;
    width: 100%;
}
.ui-selectmenu-icon.ui-icon {
    background-image: none !important;
    height: 10px;
    width: 10px;
    border-width: 5px;
    border-style: solid;
    border-color: #000 transparent transparent transparent;
    position: absolute;
    top: 27px;
    right: 27px;
    overflow: hidden;
}
.ui-state-active, .ui-widget-content .ui-state-active, 
.ui-widget-header .ui-state-active, a.ui-button:active, 
.ui-button:active, .ui-button.ui-state-active:hover {
    border-color: #000;
    background: #FFF;
}

.ui-selectmenu-menu .ui-menu .ui-menu-item-wrapper {
    height: auto;
}


// select ferias
.select-status-ferias {
    + .ui-selectmenu-button {
        height: 40px;
        line-height: 40px;
        position: relative;
        padding-left: 40px;
        color: #36bcf1;
        .ui-selectmenu-text {
            color: #36bcf1;
        }
        &:before {
            content: "\e928";
            font-family: 'icomoon' !important;
            speak: none;
            position: absolute;
            top: 0;
            left: 15px;
            color: #36bcf1;
            font-size: 14px;
        }
        .ui-selectmenu-icon.ui-icon {
            top: 18px;
            right: 18px;
        }
    }
    &.select-status-ferias-aprovado + .ui-selectmenu-button {
        color: $color-success;
        .ui-selectmenu-text {
            color: $color-success;    
        }
        &:before {
            color: $color-success;
        }
    }
    &.select-status-ferias-recusado + .ui-selectmenu-button {
        color: $color-error;
        .ui-selectmenu-text {
            color: $color-error;    
        }
        &:before {
            color: $color-error;
        }
    }
    &.select-status-ferias-aguardando + .ui-selectmenu-button {
        color: $color-waiting;
        &:before {
            color: $color-waiting;
        }
        .ui-selectmenu-text {
            color: $color-waiting;    
        }
    }
}
.select-status-materiais {
    + .ui-selectmenu-button {
        height: 40px;
        line-height: 40px;
        position: relative;
        padding-left: 40px;
        color: #36bcf1;
        .ui-selectmenu-text {
            color: #36bcf1;
        }
        &:before {
            content: "\e928";
            font-family: 'icomoon' !important;
            speak: none;
            position: absolute;
            top: 0;
            left: 15px;
            color: #36bcf1;
            font-size: 14px;
        }
        .ui-selectmenu-icon.ui-icon {
            top: 18px;
            right: 18px;
        }
    }
    &.select-status-materiais-aguardando_liberacao + .ui-selectmenu-button {
        color: $color-waiting;
        .ui-selectmenu-text {
            color: $color-waiting;    
        }
        &:before {
            color: $color-waiting;
        }
    }
    &.select-status-materiais-aguardando_reposicao + .ui-selectmenu-button {
        // color: $color-error;
        .ui-selectmenu-text {
            // color: $color-error;    
        }
        &:before {
            // color: $color-error;
        }
    }
    &.select-status-materiais-entregue + .ui-selectmenu-button {
        color: $color-success;
        &:before {
            color: $color-success;
        }
        .ui-selectmenu-text {
            color: $color-success;    
        }
    }
    &.select-status-materiais-cancelado + .ui-selectmenu-button {
