        &:before {
            color: $color-error;
        }
        .ui-selectmenu-text {
            color: $color-error;    
        }
    }

}
// jquery ui - spinner
.ui-widget.ui-widget-content.ui-spinner {
    box-shadow: none !important;
    height: 60px;
    padding: 3px;
    width: 100%;
    border: 1px solid $border-color !important;
    border-radius: 0;
    max-width: 180px;
    .add-item-group & {
        border: 1px solid $border-color !important;
    }
}
a.ui-button:active, 
.ui-button:active {
    border-color: #000;
    background: #fff;
}
.message-updated {
    font-size: 12px;
    color: $terciary-color;
    text-align: right;
    padding: 3px 0 0 0;
    display: block;
}
.ui-spinner-input {
    padding: 0 !important;
    height: 54px !important;
    font-size: 16px !important;
    text-align: center !important;
    color: #000 !important;
    position: absolute !important;
    width: 64px !important;
    left: 50% !important;
    margin: 0 0 0 -32px !important;
    @media (min-width: 991px) {
        width: 80px !important;
    }
}
.ui-spinner .ui-spinner-button {
    background: transparent;
    box-shadow: none;
    border: none;
    position: absolute;
    top: 3px;
    height: 52px;
    width: 52px;
    background: #000;
    cursor: pointer;
    .ui-icon {
        background-image: none;
        text-indent: 0;
        text-align: center;
        &:before {
            position: absolute;
            top: -2px;
            left: 0;
            font-size: 18px;
            color: #FFF;
            height: 16px;
            width: 16px;
        }
    }
    &.ui-spinner-up {
        right: 3px;
        .ui-icon {
            &:before {
                content: "+";
            }
        }
    }
    &.ui-spinner-down {
        left: 3px;
        .ui-icon {
            &:before {
                content: "-";
            }
        }
    }
}

// jquery ui - datepicker
.datepicker,
.acf-field .hasDatepicker {
    background-color: #FFF;
    height: 60px;
    width: 100%;
    border: 1px solid $border-color;
    position: relative;
    font-size: 14px;

    padding: 0 0 0 16px !important;
    -webkit-transition: border-color .2s ease;
    transition: border-color .2s ease;
}

// jquery ui - radio
.acf-field.box-radio {
    // clear: both;
}
.box-radio {
    padding: 10px 0 0 0;
}
.box-radio .ui-visual-focus {
    box-shadow: none;
}
.box-radio .ui-button {
    background: transparent;
    width: 100%;
    color: #000;
    border: none;
    text-align: left;
    padding: 0;
    margin: 0 0 20px 0;
}

.box-radio .ui-button.ui-state-active {
    background: transparent !important;
    border: inherit !important;
    color: #000;
}

.box-radio .ui-icon-background, 
.ui-state-active .ui-icon-background {
    border-color: $color-error;
}

.box-radio .ui-checkboxradio-label .ui-icon-background {
    background: transparent;
    box-shadow: none;
    border: 1px solid $terciary-color;
    margin: 0 6px 0 0;
    width: 20px;
    height: 20px;
    display: inline-block;
}
.box-radio  ul.acf-radio-list li {
    margin: 0 0 10px 0;
    font-size: 14px;
}
.box-radio ul.acf-radio-list {
    margin: 0 0 10px 0;
}

.box-radio .ui-checkboxradio-label.ui-checkboxradio-checked .ui-icon {
    background: transparent;
    border-color: $color-error;
    width: 20px;
    height: 20px;
    position: relative;
    border-width: 1px;
}
.box-radio .ui-checkboxradio-radio-label.ui-checkboxradio-checked .ui-icon:before {
    content: " ";
    position: absolute;
    top: 4px;
    left: 4px;
    background-image: none;
    width: 10px;
    height: 10px;
    border-width: 5px;
    border-style: solid;
    border-radius: 5px;
    border-color: $color-error;
}
.box-radio .ui-checkboxradio-label.ui-checkboxradio-checked .ui-icon:before {
    content: " ";
    position: absolute;
    top: 4px;
    left: 4px;
    background-image: none;
    width: 10px;
    height: 10px;
    border-width: 5px;
    border-style: solid;
    //border-radius: 5px;
    border-color: $color-error;
}

.acf-field {
    position: relative;
    .acf-error-message {
        position: absolute;
        top: -22px;
        left: 0;
        z-index: 1;
    }
}

.acf-field-repeater {
    .acf-table {
        border: none;
        background: transparent !important;
        margin: 0 0 30px 0;
        .acf-icon {
            height: 30px;
            text-align: center;
            padding: 5px 0 0 0;
            margin: 0;
            width: 30px;
        }
        .acf-fields {
            border: none !important;
            .acf-field-post-object {
                border: none;
                width: 100% !important;
                padding: 0;
                margin: 0 0 10px 0;
            }
            .acf-field-number {
                border: none;
                width: 100% !important;
                padding: 0;
            }
        }
        .acf-row-handle.remove {
            border: none;
            background-color: transparent !important;
            margin: 0;
            padding: 0;
            .-plus {
                display: none !important;
            }
        }
        .acf-row-handle .acf-icon.-minus {
            top: 50px;
        }
        .acf-row-handle.order {
            display: none;
        }
        .acf-table .acf-icon {
            /* background: red; */
            height: 30px;
            text-align: center;
            padding: 5px 0 0 0;
            margin: 0;
            width: 30px;
        }
    }
    .acf-actions.acf-hl .button.button-primary {
        background: transparent;
        color: #36bcf1;
        border: none;
        font-weight: 700;
        font-size: 14px;
        padding: 0 0 1px 26px;
        margin: 0;
    }

    .acf-hl {
        // padding: 20px 0 0 0;
    }

    .acf-actions li {
        float: left;
    }

    .acf-actions.acf-hl .button.button-primary:before {
        content: "\e900";
        font-family: 'icomoon' !important;
        speak: none;
        position: absolute;
        top: 0;
        left: 0;
        color: #36bcf1;
        font-size: 17px;
    }

    .select2-container--default {
        .select2-selection--single {
            height: 60px;
            line-height: 60px;
            width: 100%;
            background: transparent;
            border-radius: 0px;
            padding: 0px 20px;
            .select2-selection__rendered {
                line-height: 60px;
                padding: 0;
            }
            .select2-selection__arrow {
                top: 16px;
                right: 16px;
            }
        }
    }

    .acf-repeater .acf-row {
        margin: 0 0 50px 0;
    }
    .acf-field[data-width] + .acf-field[data-width] {
        border: none;
        margin: 0 0 20px 0;
    }

    @media (min-width: 991px) {
        .acf-table {
            margin: 0 0 20px;
            .acf-fields {
                .acf-field-post-object {
                    display: inline-block;
                    margin-right: 3%;
                    width: 64% !important;
                }
                .acf-field-number {
                    display: inline-block;
                    width: 29% !important;
                    margin-left: 3% !important;
                }
            }
        }
    }
}

