.section-main {
    padding: 20px 0 0 0;
    position: relative;
    .body-menu-open & {
        filter: blur(3px); /* Any browser which supports CSS3 */
        filter: url("blur.svg#gaussian_blur"); /* Firefox version 34 and earlier */
        -webkit-filter: blur(3px); /* Webkit in Chrome 52, Safari 9, Opera 39, and earlier */
    }
    body.home & {
        padding: 20px 0 0 0;
    }
    &.section-main-internal {
        padding: 0;
    }
    


    .page-aniversarios & {
        &:before  {
            height: 160px;
            width: 100%;
            background: $color-aniversarios;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 24px 0;
            overflow: hidden;
            &:after {
                content: '\e90d';
                font-family: 'icomoon' !important;
                speak: none;
                position: absolute;
                bottom: 56px;
                right: -18px;
                color: rgba(255, 255, 255, 0.1);
                font-size: 200px;
                -webkit-transform: rotate(-15deg);
                -ms-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
        }
    }
    
    .page-material-escritorio & {
        &:before  {
            height: 160px;
            width: 100%;
            background: $color-material-escritorio;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 0 0;
            overflow: hidden;
            .icon {
                margin-top: 16px;
            }
            &:after {
                content: '\e91e';
                font-family: 'icomoon' !important;
                speak: none;
                position: absolute;
                bottom: 56px;
                right: -18px;
                color: rgba(255, 255, 255, 0.1);
                font-size: 200px;
                -webkit-transform: rotate(-15deg);
                -ms-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
        }
    }

    .page-solicitacoes-ferias & {
        .btn.btn-top {
            margin-left: -110px;
        }
        &:before  {
            height: 160px;
            width: 100%;
            background: $color-ferias;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 0 0;
            overflow: hidden;
            &:after {
                content: '\e926';
                font-family: 'icomoon' !important;
                speak: none;
                position: absolute;
                bottom: 56px;
                right: -18px;
                color: rgba(255, 255, 255, 0.1);
                font-size: 200px;
                -webkit-transform: rotate(-15deg);
                -ms-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
        }
    }

    .page-template-template-conteudo & {
        .btn.btn-top {
            margin-left: -110px;
        }
        &:before  {
            height: 160px;
            width: 100%;
            background: $color-conteudo;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 0 0;
            overflow: hidden;
        }
    }

    .page-template-template-conteudo-live & {
        &:before  {
            height: 320px;
            width: 100%;
            background: $color-conteudo-live;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 0 0;
            overflow: hidden;
        }

    }

    .page-template-template-calendario & {
        .btn.btn-top {
            // margin-left: -110px;
            margin-left: 0;
            left: auto;
            right: 50px;
        }
        &:before  {
            height: 160px;
            height: 266px;
            width: 100%;
            background: $color-calendario;
            position: absolute;
            top: 0;
            left: 0;
            content: " ";
        }
        .section-main-banner {
            margin: 0 0 0 0;
            overflow: hidden;
            height: 266px;
            &:after {
                content: '\e902';
                font-family: 'icomoon' !important;
                speak: none;
                position: absolute;
                bottom: 56px;
                right: -18px;
                color: rgba(255, 255, 255, 0.1);
                font-size: 200px;
                -webkit-transform: rotate(-15deg);
                -ms-transform: rotate(-15deg);
                transform: rotate(-15deg);
            }
            .extra {
                font-size: 20px;
                color: #FFF;
                position: absolute;
                top: 16px;
                right: 20px;
            }
        }
        .box-checkboxes {
            position: absolute;
            top: 120px;
            left: 40px;
            .checkbox {
                color: #FFF;
                font-family: $secondary-font-family;
                font-size: 10px;
                font-weight: 400;
                text-transform: uppercase;
                label {
                    cursor: pointer;
                    display: inline-block;
                    position: relative;
                    &:before {
                        content: " ";
                        position: absolute;
                        top: 2px;
                        left: 0;
                        height: 14px;
                        width: 14px;
                        background: #FFF;
                    }
                    input {
                        margin: 3px 6px 0 0;
                        float: left;
                        visibility: hidden;
                    }
                }
                &.checkbox-checked {
                    label {
                        &:before {
                            background: transparent !important;
                            border: 2px solid #FFF;
                        }
                    }
                }
                //&.checkbox-feriado-nacional {
                //    label {
                //        &:before {
                //            background: #04d1a8;
                //        }
                //    }
                //}
                //&.checkbox-feriado-regional {
                //    label {
                //        &:before {
                //            background: #36bcf1;
                //        }
                //    }
                //}
                //&.checkbox-nao-trabalhamos-cc {
                //    label {
                //        &:before {
                //            background: #926cde;
                //        }
                //    }
                //}
                //&.checkbox-data-de-pagamento {
                //    label {
                //        &:before {
                //            background: #e7e014;
                //        }
                //    }
                //}
                //&.checkbox-ponte-recesso {
                //    label {
                //        &:before {
                //            background: #f2982e;
                //        }
                //    }
                //}
                //&.checkbox-inicio-atividades-2019 {
                //    label {
                //        &:before {
                //            background: #f45ace;
                //        }
                //    }
                //}
            }
        }
    }

    .section-main-banner {
        height: 160px;
        position: relative;
        .section-main-banner-internal {
            // padding: 10px 24px 15px 24px;
            position: absolute;
            top: 40px;
            left: 40px;
            // top: 50%;
            // left: 50%;
            // transform: translate(-50%, -50%);
            // -webkit-transform: translate(-50%, -50%);
            width: 80%;
        }
        .icon {
            color: #FFF;
            font-size: 46px;
            display: inline-block;
            margin: 3px 16px 16px 0;
            float: left;
        }
        .title {
            color: #FFF;
            font-family: $secondary-font-family;
            font-size: 18px;
            line-height: 26px;
            strong {
                font-size: 25px;
                display: block;
                text-transform: uppercase;
            }
        }
    }

    &.section-main-internal-office {
        .section-main-banner {
            height: 260px;
        }
        &:before  {
            height: 260px;
        }
    }

    &.section-main-internal-live {
        .box-page-content {
            margin: 0;
            .article-post-title {
                font-family: $secondary-font-family;
                font-size: 30px;
                font-weight: 500;
            }
            .article-post-date {
                font-weight: 500;
            }
        }
    }

    @media (min-width: 991px) {
        padding: 40px 0 0 0;
        .section-main-banner {
            .section-main-banner-internal {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                -webkit-transform: translateY(-50%);
            }
        }
        .page-aniversarios & {
            .section-main-banner {
                margin: 0 0 40px 0;
                overflow: visible;
                .icon {
                    margin: 3px 24px 0px 0;
                }
            }
        }
        &.section-main-internal-office {
            .section-main-banner {
                height: 160px;
            }
            &:before  {
                height: 160px;
            }
        }
        .page-template-template-calendario & {
            &:before {
                height: 160px;
            }
            .box-checkboxes {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                -webkit-transform: translateY(-50%);
                left: 480px;
                width: 360px;
                .checkbox {
                    float: left;
                    width: 50%;
                }
            }
            .section-main-banner {
                height: 160px;
                position: fixed;
                top: 60px;
                left: 0;
                width: 100%;
                background: #000;
                z-index: 2;
                &:after {
                    right: 10px;
                    font-size: 140px;
                }
                .section-main-banner-internal {
                    width: 300px;
                    left: 130px;
                }
            }
        }
    }
}
