/************** MEU PERFIL ***************************/

.page-profile {
    background: #FFF;
    padding: 40px 0 0px 0;
    margin: -20px 0 15px 0;
    .person {
        padding: 0 0 36px 0;
        text-align: center;
        border-bottom: 1px solid $border-color;
        .box-image {
            float: none;
            margin: 0 0 30px 0;
            img {
                height: 160px;
                width: 160px;
                border: 3px solid #fff;
                box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.7490196078431373);
            }
        }
        .box-content {
            display: block;
            width: 100%;
            padding: 0 15px;
        }
        .name {
            font-size: 20px;
            font-weight: 400;
            margin: 0 0 10px 0;
        }
        .role {
            font-size: 15px;
            font-weight: 400;
        }
    }
    .person-about {
        padding: 24px 15px;
        border-bottom: 1px solid $border-color;
        p {
            line-height: 20px;
            color: $terciary-color;
            strong {
                color: #000;
            }
        }
    }
    .person-item {
        padding: 20px 15px;
        border-bottom: 1px solid $border-color;
        .label {
            width: 49%;
            display: inline-block;
            font-weight: 700;
            vertical-align: top;
        }
        .info {
            color: $terciary-color;
            width: 49%;
            display: inline-block;
             /* These are technically the same, but use both */
              overflow-wrap: break-word;
              word-wrap: break-word;

              -ms-word-break: break-all;
              /* This is the dangerous one in WebKit, as it breaks things wherever */
              word-break: break-all;
              /* Instead use this non-standard one: */
              word-break: break-word;

              /* Adds a hyphen where the word breaks, if supported (No Blink) */
              // -ms-hyphens: auto;
              // -moz-hyphens: auto;
              // -webkit-hyphens: auto;
              // hyphens: auto;

              .mail {
                  color: $terciary-color; 
                  &:hover {
                      color: #000;
                  }
              }
        }
    }
    @media (min-width: 991px) {
        margin: 0;
        border: 1px solid $border-color;
        .person-item {
            &:last-child {
                border-bottom: none;
            }
        }
    }
}

.page-publisher {
    padding: 0;
    background-color: #fafafd;
    .acf-custom-publisher-wrapper {
        border-top: 1px solid $border-color;
        .acf-custom-publisher-users {
            display: none;
        }
    }
    .page-iframe-header {
        padding: 15px;
        i {
            color: $primary-color;
            float: left;
            margin: 0 8px 0 0;
        }
        .title {
            font-size: 14px;
            font-weight: 700;
        }
    }
    .box-publisher {
        margin: 0;
        border-bottom: none;
        .content-placeholder {
            top: 14px;
            left: 18px;
            max-width: 90%;
            max-height: 30px;
            + form {
                > .acf-form-submit {
                    position: relative;
                    .acf-spinner {
                        position: absolute;
                        top: -40px;
                        left: 10px;
                    }
                }
            }
        }
        .acf-custom-publisher-wrapper {
            height: 60px !important;
            border: none;
            // .acf-error-message.-success {
            //     display: none;
            // }
        }
    }
    .box-logo-dbr {
        display: none;
    }
}
.fancybox-slide--iframe .fancybox-content {
    width : 290px;
    height: 230px;
    margin: 0;
}


// PAGE CONTEUDO
.box-page-content {
    background: #FFF;
    padding: 40px 0 0px 0;
    .box-page-content-left,
    .box-page-content-right {
        padding: 0 15px;
    }
    .box-page-content-wrapper {
        position: relative;
    }
    .article-post-header {
         margin: 0 0 20px 0;
        .article-post-title {
            font-family: $secondary-font-family;
            font-size: 30px;
            margin: 0 0 4px 0;
        }
        .article-post-date {
            font-family: $secondary-font-family;
            font-size: 12px;
            color: $terciary-color;
            margin: 0 0 20px 0;
        }
        .article-post-subtitle {
            font-weight: 700;
            font-size: 16px;
            margin: 0 0 6px 0;
        }
    }
    .container-likes {
        // height: 60px;
        .article {
            margin: 0;
            .article-footer {
                margin: 0;
            }
        }
    }
    @media (min-width: 991px) {
        margin: 40px 0 40px 0;
        border: 1px solid $border-color;
        &:before,
        &:after {
            content: " ";
            display: table;
            width: 100%;
        }
        .box-page-content-left,
        .box-page-content-right {
            float: left;
            width: 50%;
        }
        .box-page-content-left {
            padding: 0 15px 0 30px;
        }
        .box-page-content-right {
            padding: 0 0 0 15px;
        }
        .container-likes {
            position: absolute;
            bottom: 1px;
            width: 100%;
            left: 0;
            .article {
                .article-footer {
                    border-width: 1px 0 0 1px;
                    border-top: 1px solid $border-color;
                    .comments {
                        max-height: 400px;
                        overflow: auto;
                    }
                }
            }

        }
        .box-page-content-wrapper {
            height: 100%;
            padding: 0 30px 0 0;
        }
    }
}
.article-post-content {
    color: $terciary-color;
    h1, h2, h3, h4, h5, h6 {
        color: $terciary-color;
        margin: inherit;
    }
    .content img {
        max-width: 100%;
        height: auto;
        display: block;
    }
    .img-highlight {
        width: 100%;
        height: auto;
    }
    p {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 30px;
        strong {
            font-weight: 700;
        }
        em {
            font-style: italic;
        }
        a {
            color: $terciary-color;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
    }
        
    .content iframe {
        max-width: 100%;
    }

    ol,
    ul {
        margin-bottom: 30px;
        margin-left: 40px;
        li,
        li {
            margin-bottom: 6px;
            list-style: disc;
            &:last-child,
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .alignleft,
    p .alignleft {
        float: left;
        margin: 0 15px 8px 0;
    }
    .aligncenter,
    p .aligncenter {
        display: block;
        margin: 0 auto 8px auto;
    }
    .alignright,
    p .alignright {
        float: right;
        margin: 0 0 8px 15px;
    }
    .alignnone,
    p .alignnone {
        float: none;
        display: block;
        margin: 0 0 8px 0;
    }
    .size-full,
    p .size-full {
        max-width: 100%;
        height: auto;
        /*margin: 0 0 30px 0;*/
    }
    .wp-caption-text {
        font-style: italic;
        font-size: 14px;
    }
    .wp-caption img {
        margin-bottom: 6px;
    }

    img.alignnone {
        display: inline-block;
    }
}
.article-accordion {
    border: 1px solid $border-color !important;
    border-bottom: none !important;
    margin: 0 0 40px 0 !important;
    .accordion-title {
        font-family: $secondary-font-family;
        font-size: 18px;
        background: transparent;
        border: none;
        border-radius: 0;
        padding: 25px 25px 22px 25px;
        border-bottom: 1px solid $border-color;
        cursor: pointer;
        &.ui-accordion-header-active {
            border: none;
        }
        .ui-accordion-header-icon {
            display: none;
        }
    }
    .accordion-item {
        border: none;
        padding: 0 25px 22px 25px;
        border-bottom: 1px solid $border-color;
        border-radius: 0;
        .accordion-text {
            color: $terciary-color;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 20px;
        }
    }
    .ui-accordion-header-active {
        &.accordion-title {
            color: $primary-color;
        }
    }
}
.box-external-link {
    margin: 0 0 40px 0;
    .btn {
        display: inline-block;
        height: 60px;
        line-height: 60px;
        background-color: #282931;
        padding: 0 36px;
    }
}

.section-main-live {
    background-color: $color-conteudo-live;
    .box-player {
        float: left;
        padding: 18px 0;
        height: 320px;
        width: 500px;
        margin-right: 30px;
    }
    .box-content {
        padding: 42px 30px 42px 0;
        .top-hat,
        .title,
        .date,
        .text {
            color: #FFF;
        }
        .top-hat {
            font-family: $secondary-font-family;
            font-size: 18px;
            margin-bottom: 8px;
        }
        .title {
            font-family: $secondary-font-family;
            font-weight: 700;
            font-size: 35px;
            margin-bottom: 8px;
        }
        .date {
            color: #c5c6c7;
            font-family: $secondary-font-family;
            font-size: 16px;
            margin-bottom: 16px;
            font-style: italic;
        }
        .text {
            color: #848796;
            line-height: 20px;
        }
    }
    @media (max-width: 991px) {
        .box-content {
            padding: 42px 20px;
        }
        .box-player {
            max-width: 100%;
            height: 280px;
            iframe,
            object,
            embed {
                width: 100%;
            }
        }
    }
}
    
.box-player-internal {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 0px; 
    height: 0; 
    overflow: hidden;
    iframe,
    object,
    embed {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}