.header {
    background-color: $secondary-color;
    height: 60px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
    .body-menu-open & {
        position: absolute;
    }
    .logo {
        background-color: $primary-color;
        height: 60px;
        float: left;
        width: 180px;
        position: relative;
        img {
            height: auto;
            width: 121px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
        }
    }
    .header-btns {
        float: right;
        width: 140px;
        text-align: right;
        padding: 19px 10px 0 10px;
        position: relative;
        .btn-notification {
            position: relative;
            .has-notification {
                background-color: $primary-color;
                height: 12px;
                width: 12px;
                position: absolute;
                top: 0;
                left: 50%;
                border: 2px solid $secondary-color;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                display: none;
            }
            &.btn-notification-has-notification {
                .has-notification {
                    display: block;
                }
            }
        }
    }
    .header-container {
        .box-search {
            display: none;
            background: #282931;
            width: 90%;
            height: 40px;
            font-size: 12px;
            color: $terciary-color;
            font-weight: 300;
            border: none;
            padding: 10px;

            position: absolute;
            top: 60px;
            z-index: 1;
            width: 100%;
            height: 60px;
            .search-label {
                color: #51525e;
                cursor: pointer;
                height: 40px;
                width: 40px;
                position: absolute;
                top: 20px;
                right: 10px;
                // transform: scaleX(-1);
                // -moz-transform: scaleX(-1);
                // -webkit-transform: scaleX(-1);
                // -ms-transform: scaleX(-1);
                i {
                    font-size: 20px;
                }
            }
            .input-text {
                background: #282931;
                width: 90%;
                height: 40px;
                font-size: 12px;
                color: $terciary-color;
                font-weight: 300;
                border: none;
                padding: 0 20px;
            }
            input {
                @include placeholder (#f13d35);
            }
        }
        .profile {
            
        }
        .header-container-menu {
            background-color: #FFF;
            display: none;
            position: absolute;
            top: 60px;
            width: 100%;
            z-index: 1;
        }
        .nav {
            display: block;
            padding: 15px 5px 0 5px;
            margin-bottom: 20px;
            width: 100%;
            .nav-item {
                border-left: 1px solid $border-color;
                border-right: 1px solid $border-color;
                border-top: 1px solid $border-color;
                display: inline-block;
                margin: 0 9px 20px 9px;
                vertical-align: top;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    @media (min-width: 991px) {
        .header-container {
            .box-search {
                display: inline-block;
                top: 0;
                width: 420px;
            }
        }
        .header-btns {
            // background-color: red;
            // width: auto;
        }
    }
    @media (min-width: 991px) {
        .header-container {
            .header-container-menu {
                display: inline-block;
                top: 0;
                width: auto;
                z-index: 2;
                right: 15px;
                background: none;
                float: right;
                position: relative;
                height: 60px;
                .nav {
                    display: none;
                }
                .profile {
                    float: left;
                    .btn-toogle-profile {
                        color: #FFF;
                        font-size: 10px;
                    }
                    .profile-trigger {
                        padding: 15px 60px 0px 15px;
                    }
                    .profile-container {
                        position: absolute;
                        top: 55px;
                        background: #FFF;
                        right: 0;
                        width: 230px;
                        border: none;
                        box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
                        &:before {
                            content: " ";
                            position: absolute;
                            top: -16px;
                            right: 18px;
                            border-color: transparent transparent #FFF transparent;
                            border-style: solid;
                            border-width: 8px 8px 8px 8px;
                        }
                    }
                    .box-content {
                        padding: 2px 0 0 42px;
                        .name {
                            color: #FFF;
                        }
                        .role {
                            color: $terciary-color;
                        }
                    }
                    .profile-nav {
                        .profile-nav-item {
                            margin: 0 0 14px 0;
                            &:last-child {
                                margin-bottom: 0;
                            }
                        }
                    }
                }
            }
        }
        .header-btns {
            padding: 21px 24px 0 10px;
            .btn {
                font-size: 20px;
            }
        }
        .box-widget,
        .logo-dbr {
            display: none;
        }
    }
    @media (min-width: 1200px) {
        //background-image: url(../images/app-store@3x.png)   
    }
}
@media (max-width: 991px) {
    .body-menu-open {
        .ui-menu.ui-widget.ui-widget-content.ui-autocomplete.ui-front {
            z-index: 9999;
        }
    }
}

.ui-menu.ui-autocomplete {
    max-height: 370px;
    overflow-y: auto;
    position: fixed;
    @media (max-width: 991px) {
        max-width: 100% !important;
        width: 100% !important;
    }
}

.box-notifications {
    display: none;
    position: absolute;
    top: 60px;
    left: 0;
    z-index: 21;

    &.loading {
        .box-notifications-internal {
            height: 100px;
            overflow: hidden;
            &:before {
                background-color: #FFF;
                content: " ";
                position: absolute;
                top: 0;
                left: 0;
                height: 100px;
                width: 100%;
                z-index: 1;
            }
            &:after {
                content: " ";
                position: absolute;
                top: 50%;
                left: 50%;
                height: 30px;
                width: 30px;
                margin: -15px 0 0 -15px;
                background-image: url(../images/spinner@2x.gif);
                background-size: 100%;
                z-index: 1;
            }
        }
    }

    .box-notifications-internal {
        border-top: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        overflow-y: scroll;
        width: 320px;
        text-align: left;
        max-height: 380px;
        position: relative;
    }

    .box-notifications-header {
        padding: 14px 0 14px 20px;
        border-bottom: 1px solid $border-color;
        h6 {
            font-family: $secondary-font-family;
            font-size: 10px;
            color: $terciary-color;
        }
    }

    .box-notifications-body {
        .notification-item {
            border-bottom: 1px solid $border-color;
            padding: 20px;
            &:before,
            &:after {
                content: " ";
                display: table;
                width: 100%;
            }
            .box-image {
                float: left;
                margin: 3px 6px 0px 0;
            }
            .box-content {
                width: 84%;
                float: left;
                padding-right: 30px;
            }
            .notification-text {
                color: $terciary-color;
                font-size: 12px;
                line-height: 16px;
                span {
                    font-weight: 700;
                    color: #000;
                }
            }
            .notification-date {
                color: #aeb1be;
                font-weight: bold;
                font-size: 10px;
            }
            &.notification-item-default,
            &.notification-item-material,
            &.notification-item-ferias,
            &.notification-item-post,
            &.notification-item-like,
            &.notification-item-comment {
                .box-content {
                    position: relative;
                    &:after {
                        content: '\e90a';
                        font-family: 'icomoon' !important;
                        speak: none;
                        position: absolute;
                        top: 8px;
                        right: 0;
                        color: rgba($terciary-color, 0.5);
                        font-size: 20px;
                    }
                }
            }
            &.notification-item-default{
                .box-content {
                    &:after {
                        content: '\e90a';
                    }
                }
            }
            &.notification-item-material{
                .box-content {
                    &:after {
                        content: '\e90a';
                    }
                }
            }
            &.notification-item-ferias{
                .box-content {
                    &:after {
                        content: '\e90a';
                    }
                }
            }
            &.notification-item-post{
                .box-content {
                    &:after {
                        content: '\e90a';
                    }
                }
            }
            &.notification-item-like{
                .box-content {
                    &:after {
                        content: '\e908';
                    }
                }
            }
            &.notification-item-comment {
                .box-content {
                    &:after {
                        content: '\e90a';
                    }
                }
            }
        }
    }

    @media (min-width: 991px){
        position: absolute;
        top: 55px;
        left: -60px;
        
        .box-notifications-internal {
            background: #FFF;
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);
            width: 320px;

            
        }
        &:before {
            content: " ";
            position: absolute;
            top: -15px;
            left: 50%;
            margin-left: -4px;
            border-color: transparent transparent #FFF transparent;
            border-style: solid;
            border-width: 8px 8px 8px 8px;
            z-index: 1;
        }
    }
}
