.slider-alert {
    background-color: #FFF;
    border: 1px solid $border-color;
    position: relative;
    margin: 0 15px 20px 15px;
    display: none;
    &.slick-initialized {
        display: block;
    }
    .slick-slide {
        // height: 260px;
    }
    .slide {
        padding: 35px 15px 40px 15px;
        // height: 260px;
        position: relative;
        a {
            display: block;
            height: 100%;
            width: 100%;
        }
        .slider-icon {
            background: $primary-color;
            position: absolute;
            top: 0;
            left: 0;
            color: #FFF;
            padding: 6px 10px 10px 3px;
            border-radius: 0px 0px 50px 0px;
            font-size: 14px;
            width: 34px;
            height: 36px;
            text-align: center;
        }
        .slider-title {
            font-family: $secondary-font-family;
            font-weight: 300;
            font-size: 32px;
            color: $primary-color;
            text-align: center;
            margin: 0 0 12px 0;
        }
        .slider-text {
            color: $terciary-color;
            font-size: 14px;
            font-style: italic;
            line-height: 24px;
        }
    }
    .slide-image {
        padding: 0;
        height: 100px;
        a {
            background-size: cover;
            background-position: center center;
            text-indent: -9999px;
        }
    }
    @media (min-width: 991px) {
        margin: 0 0 20px 0;
        .slick-slide {
            // height: 100px;
        }
        .slide {
            padding: 25px 15px 22px 15px;
            // height: 100px;
            .slider-title {
                width: 25%;
                float: left;
                padding: 6px 10px 0 10px;
            }
            .slider-text {
                width: 75%;
                float: left;
                padding: 0 20px 0 0px;
            }
            .slide {
                padding: 25px 0 22px 0 !important;
            }
        }
        .slide-image {
            padding: 0;
            height: 100px;
        }
    }
}